  .progressbar,
  .progressbar-value,
  .progressbar-cover {
    width: 253px;
    height: 18px;
  }
  
  .progressbar {
    position: relative;
    background-color: #eee;
    margin-top: 10px;
  }
  
  .progressbar-value {
    position: absolute;
    display: block;
    margin: 0;
    border: 0;
    overflow: hidden;
    background-color: #0cf;
    text-indent: -30px;
  }
  
  .progressbar-cover {
    position: absolute;
    display: block;
    border: 0;
    left: 0%;
    top: 0;
    background-color: #eee;
  }
  
  ol.intro li:last-child {
    border-bottom: none;
  }
  
  .intro {
    font-size: 17px;
    list-style-position: outside;
    font-weight: bold;
    width: 680px;
    color: #1a578a;
  
    counter-reset: li; /* Initiate a counter */
    margin-left: 50px; /* Remove the default left margin */
    padding-left: 0; /* Remove the default left padding */
  }
  
  ol.intro > li {
    position: relative; /* Create a positioning context */
    list-style: none; /* Disable the normal item numbering */
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 40px;
    line-height: 30px;
    padding: 5px 10px 10px 10px;
    border-bottom: 1px dotted #ccc;
  }
  
  ol.intro > li:before {
    content: " "; /* Use the counter as content */
    /* Position and style the number */
    position: absolute;
    left: -26px;
    box-sizing: border-box;
    top: 2px;
    width: 25px;
    color: #999;
    font-style: italic;
    font-family: Georgia, serif;
    font-weight: bold;
    font-size: 25px;
    text-align: center;
  }
  
  .multipleChoices li,
  .dd .ddMenu li {
    background: url(data:image/.png;base64,)
      no-repeat 2px 5px;
    cursor: pointer;
    list-style: none;
  }
  
  .multipleChoices li.selected,
  .dd .ddMenu li.selected {
    background-image: url(data:image/.png;base64,);
    text-decoration: underline;
  }
  
  .multipleChoices li {
