  .dd {
    display: inline;
    margin: 0;
    padding: 0;
  }
  
  .dd .ddMenu {
    display: none;
    background-color: #fff;
    color: #000;
    padding: 0.6em;
    padding: 20px 20px 20px 10px;
    border-radius: 10px;
    border: 3px solid #bbb;
    margin-top: 0px;
    position: absolute;
  }
  
  .dd .ddMenu ul {
    margin-left: 10px;
  }
  
  .dd .ddMenu li {
    margin-left: 0;
    padding-left: 25px;
    padding-top: 4px;
    padding-bottom: 3px;
    padding-right: 10px;
    border-bottom: 1px dotted #ccc;
