  p.bubble {
    position: relative;
    width: 300px;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
    margin: 30px auto;
    background-color: #fff;
    border: 4px solid #666;
    -webkit-border-radius: 30px;
    -moz-border-radius: 30px;
    border-radius: 30px;
    display: none;
  }
  
  p.bubble:before,
  p.bubble:after {
    content: " ";
    position: absolute;
    width: 0;
    height: 0;
  }
  
  p.speech:before {
    left: 50px;
    top: -50px;
    border: 25px solid;
    border-color: transparent transparent #666 transparent;
  }
  
  p.speech:after {
    left: 56px;
    top: -37px;
    border: 19px solid;
    border-color: transparent transparent #fff transparent;
  }
  
