@font-face {
  font-family: "Fira Sans";
  src: url("FiraSans-Regular.woff");
}

body {
  background-attachment: fixed;
  background: #fff
    url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAAADCAYAAABWKLW/AAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABlJREFUeNpi+P//PwMIA4EAggEFcAZAgAEAHHwJKIgcPX8AAAAASUVORK5CYII=);
  font-family: "Fira Sans", "Trebuchet MS", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  color: #53565a !important; /*Paragon grey*/
}

header {
  min-width: 1000px;
}

#login-dropdown {
  padding: 15px;
  padding-bottom: 0px;
}

#login-dropdown input[type="text"],
#login-dropdown input[type="password"] {
  margin-bottom: 15px;
  width: 180px;
}

#login-dropdown input[type="submit"] {
  margin-bottom: 15px;
  clear: left;
  width: 100%;
}

#sign-in-link {
  font-family: Fira Sans;
  margin: 0 0 0 -10px;
  color: #53565a;
  width: 70px;
  height: 35px;
  text-decoration: none;
  font-size: 12pt;
}

#sign-in-link:hover {
  color: #b3a369;
}

header .nav .dropdown a {
  cursor: pointer;
}

header #selected-product {
  color: #666666;
  background-color: #ffffff;
  padding: 2px 7px;
  font-size: 12px;
  border-radius: 0px;
}

#main-container {
  background-color: #fff;
  height: 756px;
  overflow-y: auto;
  width: 980px;
  border: 1px solid #ccc;
  padding: 0;
}

#main-container .row1 {
  padding-left: 20px;
  padding-right: 20px;
}

#footer {
  width: 960px;
  padding-left: 20px;
  border: 1px solid #ccc;
  border-top-width: 0;
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: #ebebeb;
  text-align: right;
}

a {
  color: #a6192e; /*Paragon red: #A6192E*/
  text-decoration: none;
}

a:hover,
a:focus {
  color: #a6192e; /*Paragon red: #A6192E*/
  text-decoration: underline;
}

h0 {
  font-weight: bolder;
  font-size: 60px;
}

h1 {
  font-size: 18px;
  border-bottom: 1px solid #ddd;
  text-align: center;
  padding: 2px 20px;
  text-shadow: 1px 1px 0 #fff;
  margin-top: 0;
  background: rgb(238, 238, 243); /* Old browsers */
  /* IE9 SVG, needs conditional override of 'filter' to 'none' */
  background: url(data:image/svg+xml;base64,);
  background: -webkit-linear-gradient(
    top,
    rgba(238, 238, 243, 1) 0%,
    rgba(215, 219, 224, 1) 100%
  ); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(
    top,
    rgba(238, 238, 243, 1) 0%,
    rgba(215, 219, 224, 1) 100%
  ); /* Opera 11.10+ */
  background: linear-gradient(
    top,
    rgba(238, 238, 243, 1) 0%,
    rgba(215, 219, 224, 1) 100%
  ); /* W3C */
}

h2 {
  font-size: 24.5px;
  font-family: "Fira Sans", "Century Gothic", CenturyGothic, "AppleGothic",
    sans-serif;
}

h3 {
  font-size: 17.5px;
}

h4 {
  font-size: 14px;
}

h5 {
  font-size: 11.9px;
}

h6 {
