#noscript {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #fff;
  opacity: 0.9;
  z-index: 100000;
  top: 0;
  left: 0;
}

#noscript div {
  width: 500px;
  height: 200px;
  margin: 0 auto;
  opacity: 1;
  font-size: 30px;
  vertical-align: middle;
  color: #000;
  margin-top: 100px;
}

#IPThumnailGrid {
