.header-buttons > li > a {
  border-radius: 5px;
  padding: 5px 0px !important;
  color: #ffffff !important;
  width: 75px;
  height: 16px;
  /*text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);*/
