  *background-color: #a6192e;
  background-image: -moz-linear-gradient(top, #a6192e, #a6192e);
  background-image: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    from(#a6192e),
    to(#a6192e)
  );
  background-image: -webkit-linear-gradient(top, #a6192e, #a6192e);
  background-image: -o-linear-gradient(top, #a6192e, #a6192e);
  background-image: linear-gradient(to bottom, #a6192e, #a6192e);
  background-repeat: repeat-x;
