}

.navbar {
  border-radius: 0;
}

.navbar-inner {
  height: 75px;
  padding-right: 20px;
  padding-left: 20px;
  background-color: #fafafa;
  background-image: -moz-linear-gradient(top, #ffffff, #ffffff);
  background-image: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    from(#ffffff),
    to(#ffffff)
  );
  background-image: -webkit-linear-gradient(top, #ffffff, #ffffff);
  background-image: -o-linear-gradient(top, #ffffff, #ffffff);
  background-image: linear-gradient(to bottom, #ffffff, #ffffff);
  background-repeat: repeat-x;
  border: 1px solid #d4d4d4;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffffff', endColorstr='#ffffff', GradientType=0);
  *zoom: 1;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);
  -moz-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);
}

.navbar .brand {
  width: 225px;
  margin-top: 5px;
  margin-bottom: 5px;
  color: #53565a;
}

.navbar .nav > li > a {
  float: none;
  padding: 10px 15px 10px;
  color: #a6192e;
  text-decoration: none;
  text-shadow: 0 0px 0 #ffffff;
}

.navbar .nav > li > a:focus,
.navbar .nav > li > a:hover {
  color: #a6192e;
  text-decoration: none;
  background-color: transparent;
}

.dropdown-menu {
  border-radius: 0px !important;
  background-color: #a6192e !important;
}

.dropdown-menu > li > a {
  color: #ffffff;
  text-transform: uppercase;
}

.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus,
.dropdown-submenu:hover > a,
.dropdown-submenu:focus > a {
  color: #ffffff;
  text-decoration: none;
  background-color: #7a1222 !important;
  background-image: -moz-linear-gradient(top, #7a1222, #7a1222);
  background-image: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    from(#7a1222),
    to(#7a1222)
  );
  background-image: -webkit-linear-gradient(top, #7a1222, #7a1222);
  background-image: -o-linear-gradient(top, #7a1222, #7a1222);
  background-image: linear-gradient(to bottom, #7a1222, #7a1222);
  background-repeat: repeat-x;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#7a1222', endColorstr='#7a1222', GradientType=0);
}

.dropdown-email {
  font-size: medium;
  text-transform: uppercase;
  color: #53565a !important;
}

.dropdown-email:active,
.dropdown-email:hover,
.dropdown-email:focus {
  font-size: medium;
  text-transform: uppercase;
  color: #b3a369 !important; /*Paragon gold*/
}

